@import url(https://fonts.googleapis.com/css?family=Poppins:400,700&subset=latin-ext);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'KuranKerimFontHamdullah';
  font-style: normal;
  font-weight: 900;
  src: url(/tedzvid/static/media/KuranKerimFontHamdullah.c5109a1c.ttf);
  /* IE9 Compat Modes */ }

*, ::after, ::before {
  box-sizing: unset; }

body {
  font-family: Palatino, "Palatino Linotype";
  font-size: 1.7rem; }

#root {
  display: grid;
  grid-template-columns: 1fr;
  place-items: center center;
  height: 100vh; }

.underLine {
  font-family: 'KuranKerimFontHamdullah', serif; }

span.ne-svira {
  color: inherit; }

.oznaceno {
  color: red; }

span.svira {
  font-family: 'KuranKerimFontHamdullah', serif;
  /* display: inline-block; */
  /* border: 1px solid #ddd; */
  /* padding: 0 .25em; */
  /* border-radius: .15em; */
  /* padding-top: .35em; */
  /* margin-left: .5rem; */
  color: green;
  font-weight: bold; }
  span.svira .oznaceno {
    /* color: green; */ }

.bosanski {
  font-size: 1.8rem;
  padding: 0 .25rem; }

span.tacka {
  padding-left: 0.3em;
  padding-right: 0.3em; }

span.after {
  padding-left: 0.3em;
  padding-right: 0.3em;
  font-size: 2em;
  font-family: KuranKerimFontHamdullah; }

span.arapski {
  font-family: 'KuranKerimFontHamdullah', serif;
  border-radius: .15em;
  line-height: 2em;
  font-size: 1.5em; }

span.arapski-lekcija {
  font-family: 'KuranKerimFontHamdullah', serif;
  border-radius: .15em;
  font-size: 1.6em; }

.text-center.col > span {
  display: inline-block; }

span.arap-red {
  font-family: 'KuranKerimFontHamdullah', serif;
  color: red; }

.opisLekcije {
  padding: .8rem;
  padding-left: 4em;
  padding-right: 4em; }

.lekcije-wrapper {
  border: 20px solid;
  border-image-slice: 24 33 25 34;
  border-image-width: 20px 20px 20px 20px;
  border-image-outset: 0px 0px 0px 0px;
  border-image-repeat: repeat repeat;
  border-image-source: url("https://i.imgur.com/t6U68wJ.jpg");
  padding: .5em 1em;
  position: relative; }

footer.mainfooter {
  padding-top: .8em;
  border-top: 1px solid #ddd;
  margin-top: .5em; }

.homearea {
  font-size: medium;
  font-size: initial; }
  .homearea a:hover {
    text-decoration: none; }

.card-body:hover {
  background: #f6f6f6; }

span.imelekcije {
  float: right;
  font-size: 1rem;
  font-weight: 400; }

/* MENI */
nav.mainmenu {
  margin-bottom: 1em; }

ul.mainmenulist {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  list-style-type: none;
  text-align: center;
  padding: 0;
  margin-left: -1em;
  margin-right: -1em;
  margin-top: -0.5em;
  margin-bottom: 0; }
  ul.mainmenulist li {
    background: green;
    color: white;
    padding: .25em 0; }
    ul.mainmenulist li:nth-child(1), ul.mainmenulist li:nth-child(2), ul.mainmenulist li:nth-child(3), ul.mainmenulist li:nth-child(4) {
      border-right: 1px solid white; }
    ul.mainmenulist li:hover {
      background: #3CA84B; }

.table td {
  vertical-align: middle; }

ul.bottomMenu {
  list-style-type: none;
  padding: 0;
  background: #f2f2f2; }
  ul.bottomMenu li {
    padding: 0.25em; }
    ul.bottomMenu li:nth-child(1), ul.bottomMenu li:nth-child(2), ul.bottomMenu li:nth-child(3) {
      border-right: 1px solid white;
      border-bottom: 1px solid white; }
  ul.bottomMenu a.menulink {
    color: #666; }

.bottomMenuMain {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  position: absolute;
  width: 100%;
  z-index: 1;
  margin-left: -1em;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0; }

